@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap:100,300,400,700);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);

*
{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}

.heading_style{
  padding: 30px 0;
  text-align: center;
  font-family: "raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #ffffff;
  font-size: 1.5rem;
  margin-bottom: 30px;
}

.cards{
  width: 100%;
  height: auto;
}

.card{
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #ffffff;
  width: 21.25;
  border-radius: 12px;
  box-shadow: 0px, 13px, 10px, -7px rgba(0,0,0,0.1);
  float: left ;
}

.card:hover{
  box-shadow: 0px, 30px, 18px, -8px rgba(0,0,0,0.1);
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}

.card_img{
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card_info{
  background-color: #ffffff;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: #ffffff;
}

.card_category{
  font-family: "raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #ffffff;
}

.card_title{
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "raleway", sans-serif;
  background-color: #ffffff;
  text-transform: capitalize;
}

button{
  padding: 5px, 8px;
  font-family: "raleway, sans-serif";
  text-transform: uppercase;
  font-size: 13px;
  text-align: center;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #ffffff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;

}

@media (max-width: 768px){
  .card{
    float: none;
    margin: 5%;
    width: 90%;
  }
}

